@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{margin:0;padding:0;box-sizing:border-box;font-family:"Source Code Pro",monospace}.page-container{text-align:center;max-width:100%}.page-container ul{display:inline-flex;align-content:center;justify-content:center;max-width:400px;padding:0;margin-top:40px}.page-container li{margin:0;list-style-type:none;margin:0 15px;padding:5px;background:#ebeba8;display:inline-flex;align-content:center}.page-container li p{box-sizing:border-box;text-align:center;font-size:30px;margin:0 auto;font-weight:bold;color:#612058}.page-container li button{font-size:28px;width:40px;height:40px}.page-container .btn-wrapper{background:#ebeba8;width:185px;height:auto;display:inline-flex;align-items:center;justify-content:center;box-sizing:border-box}.page-container .btn-wrapper button.reset-count-btn{padding:10px;font-size:20px;font-family:"Source Code Pro",monospace}.page-container button,.page-container [type='button'],.page-container [type='reset'],.page-container [type='submit']{background-color:#da5baf;color:#fff;font-weight:normal;border:solid 1px #c4529e;box-shadow:-4px 4px 0 #ae498c}.page-container button:focus,.page-container [type='button']:focus,.page-container [type='reset']:focus,.page-container [type='submit']:focus,.page-container button:hover,.page-container [type='button']:hover,.page-container [type='reset']:hover,.page-container [type='submit']:hover{outline:none;border:solid 1px #ae498c;cursor:pointer}.page-container button:active,.page-container [type='button']:active,.page-container [type='reset']:active,.page-container [type='submit']:active{outline:none;position:relative;top:2px;left:-2px;box-shadow:-2px 2px 0 #99407b}

